const Users = () =>{
    const [repo, setRepo] = useState([]);
    const [error, setError] = useState(null);

    useEffect(() => {
        axios("https://reqres.in/api/users?page=1")
          .then((response) => {
            console.log(response)
            setRepo(response.data.data);
            setError(null);
          })
          .catch(setError);
      }, [error]);
    return(
        <div className="bg-violet-800 max-h-fit">
            <div className="flex justify-between items-center text-center">
            <h1 className="ml-40 mb=6 text-xl text-white mt-8">Team Members</h1>
            <input className="mr-40 mb-6 bg-amber-400 mt-8 rounded-md
            placeholder:text-white p-3 text-center shadow-sm 
            focus:outline-none focus:border-sky-500 focus:ring-sky-500 focus:ring-1 sm:text-sm" type="text" placeholder="+Invite a New User" />
            </div>
        {repo.map((user)=>{
            return(
            <div className="flex gap-x-20 mb-10 border-2 justify-center mr-40 ml-40 rounded-xl bg-slate-100" key={user.id}>
            <div className="container"><img src={user.avatar} alt="user" className="w-16 rounded-full h-14 mt-2 ml-10"/></div>
            <div className="container"><h3 className="font-bold">{user.first_name} {user.last_name}</h3></div>
            <div className="container"><h4>{user.email}</h4></div>
            {/* <h4>{user.occupation}</h4> */}
            <div className="container"><button className="border-2 pl-6 pr-6 pb-2 pt-2 mt-2 mb-6 bg-green-600 rounded-md">Activate</button></div>
            <div className="container"><img src={iconss} alt="iconss" className="w-8 h-10 mt-2" /></div>
            </div>
            )
        })}

    {/* <Stack spacing={2}>
      <Users2 count={10} shape="rounded" />
      <Users2 count={10} variant="outlined" shape="rounded" />
    </Stack> */}
        </div>
    )
}
export default Users;
